const { makeAutoObservable } = require("mobx");

class tgwa {
  tg = undefined;

  constructor() {
    makeAutoObservable(this);
  }

  async startWebApi(tg) {
    await console.log(tg);
    await this.setTg(tg);
    this.tg.expand()
  }

  async setTg(tg) {
    this.tg = tg;
  }
}

export default new tgwa();
