import React, { useState } from "react";
import MenuButton from "../../../components/MenuButton/MenuButton";
import styles from "./styles.module.scss";

const MainPage = () => {
  const [menu, setMenu] = useState([
    { id: 1, className: "menubutton", to: "rules", text: " " },
    { id: 2, className: "menubutton", to: "payment", text: " " },
    { id: 3, className: "menubutton", to: "*", text: " " },
    { id: 4, className: "menubutton", to: "*", text: "" },
    { id: 5, className: "menubutton", to: "*", text: " " },
    { id: 6, className: "menubutton", to: "*", text: "IT-" },
    { id: 7, className: "menubutton", to: "*", text: " " },
    { id: 8, className: "menubutton", to: "*", text: " " },
    { id: 9, className: "menubutton", to: "*", text: "  " },
  ]);
  return (
    <div className={styles.buttonContainer}>
      {menu.map((Button) => {
        return <MenuButton menubutton={Button} key={Button.id} />;
      })}
    </div>
  );
};

export default MainPage;
